export default {
	data() {
		return {
			username: '',
			password: ''
		}
	},
	onLoad() {

	},
	methods: {
		handleLogin() {
			// 简单验证
			if (this.username.length === 0) {
				uni.showToast({
					title: '用户名不能为空',
					icon: 'none',
					duration: 2000
				})
				return
			}
			if (this.password.length === 0) {
				uni.showToast({
					title: '密码不能为空',
					icon: 'none',
					duration: 2000
				})
				return
			}
			uni.showLoading({
				title: '请稍等...'
			})
			uni.request({
				url: getApp().globalData.baseUrl + '/login',
				method: "POST",
				data: {
					username: this.username,
					password: this.password
				},
				success: (res) => {
					let status = res.data.status
					uni.hideLoading()
					if (status === 200) {
						uni.showToast({
							title: '登录成功!\r\n页面跳转中...',
							icon: 'none',
							duration: 2000
						})
						// getApp().globalData.userInfo = res.data.data
						// 修改原有的全局对象为本地缓存
						getApp().globalData.setGlobalUserInfo(res.data.data)
						uni.switchTab({
							url:'../index/index'
						})
					} else if (status === 500) {
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						})
					}
				},
				fail: (err) => {
					uni.hideLoading()
					uni.showToast({
						title: '服务器炸了(￣▽￣")',
						icon: 'none',
						duration: 2000
					})
				}
			})
		}
	}
}
